import './App.css';
import React, {useRef, useEffect, useState} from 'react';
import ReactDOM from 'react-dom';


// intor page

function App() {
  const textRef = useRef();
  const [header, setHeader] = useState();
  const [cursor, setCursor] = useState();
  useEffect(()=>{
    const content1 = "권영민의 포트폴리오페이지 입니다.";
    const content2 = "\n제 페이지에 오신걸 환영합니다."
    let text = "";
    let cursor = "_";
    let index = 0;
    let interval = setInterval(() => {
      if(cursor === "_"){
        setCursor(cursor);
        cursor = '';
      } else{
        setCursor(cursor);
        cursor = '_'
      }
      index++;
      if(index === 5){
        setCursor('');
        clearInterval(interval);
        index = 0;

        interval = setInterval(() => {
          text += content1[index++]
          setHeader(text);
          if(index === content1.length){
            clearInterval(interval);
            index = 0;

            interval = setInterval(() => {
              if(cursor === "_"){
                setCursor(cursor);
                cursor = '';
              } else{
                setCursor(cursor);
                cursor = '_'
              }
              index++;
              if(index === 5){
                setCursor('');
                clearInterval(interval);
                index = 0;

                interval = setInterval(() => {
                  text += content2[index++]
                  setHeader(text);
                  if(index === content2.length){
                    clearInterval(interval);
                    interval = setInterval(() => {
                      if(cursor === "_"){
                        setCursor(cursor);
                        cursor = '';
                      } else{
                        setCursor(cursor);
                        cursor = '_'
                      }
                    }, 500);
                  }
                }, 200);
              }
            }, 500);
          }
        }, 200);
      }
    }, 500);
  },[]);
  
  return (
    <>
      <div className="App">
          <div className="App-header" ref={textRef}>
            {header}{cursor}
          </div>
      </div>
    </>
  );
}

export default App;
